    v-app
      v-form(@submit.prevent="onSubmit")
        v-stepper(v-model='e6' vertical)
          v-stepper-step(:complete='e6 > 1' step='1' color="#677d35" complete)  
          v-stepper-content(step='1')
            v-card.step1.mb-12(outlined)
              p , , , 
                br
                br
                |  GP photolab,)
                br
                |  GP photolab
                br
                |  , / 4-6/ 
                br
                |  GP photolab, Email
                br
                |  , GP photolabEmail, , : , 
                br
                |  , 
                br
                |  : , , 
            v-card.step1.mb-12(outlined)
              P 2020- - 85
                br
                | 
                br
                | , , 85
                br
                | 
                br
                | 2020
                br
                | 
                br
                | P, 
            v-card.step1.mb-12(outlined)
              P  GP2021 (
                br
                | GP2021, , 2021, , , , 
            v-checkbox(v-model='agreeStatement' color="#677d35" label="")
            v-btn(rounded color='#677d35' @click='e6 = 1' dark) 
            v-btn(rounded color='#677d35' :disabled="invalid" @click='e6 = 2' href='#' dark) 
          v-stepper-step(rounded :complete='e6 > 2' step='2' color="#677d35") 
          v-stepper-content(step='2')
            v-card.step2.mb-12
              h3 
              v-radio-group(v-model='project' mandatory)
                v-radio(color="#677d35" value="" label="/ 2020820212,   (1,2)")
                v-radio(color="#677d35" value="" label="/ 20209()")
                v-radio(color="#677d35" value="" label="/ ")
                v-radio(color="#677d35" value="" label="/ GP")
                v-radio(color="#677d35" value="" label="/ GP")
                v-radio(color="#677d35" value="" label=" /; GP; 9")
                v-radio(color="#677d35" value="" label=" /; GP; 9")
                v-radio(color="#677d35" value="" label="  /; GP")
                v-radio(color="#677d35" value="" label=" /; GP")
              h3 
              v-chip-group(v-model='photographer' column multipl emandatory color="#677d35")
                v-chip(value="GP" filter outlined) GP
                v-chip(value="" filter outlined) 
                v-chip(value="" filter outlined) 
                v-chip(value="" filter outlined) 
              h3 
              v-col(cols='12' sm='6' md='4')
                v-dialog(ref='dialog' v-model='modal' :return-value.sync='date' persistent width='350px')
                  template(v-slot:activator='{ on, attrs }')
                    v-text-field(
                      v-model='date'
                      label="Select Date"
                      prepend-icon='mdi-calendar'
                      readonly v-bind='attrs'
                      v-on='on'
                      color="#677d35"
                      clearable
                      @click:clear="date = null"
                      :allowed-dates="allowedDates"
                      )
                  v-date-picker(
                    v-model='date'
                    scrollable
                    color="#677d35"
                    :min="new Date().toISOString()")
                    v-spacer
                    v-btn(text color='#677d35' rounded @click='modal = false') 
                    v-btn(text color='#677d35' rounded @click='$refs.dialog.save(date)') 
            v-btn(rounded color='#677d35'  @click='e6 = 1' href='#' dark) 
            v-btn(v-if="(this.$data.agreeStatement === false || this.$data.date === null || this.$data.photographer === null)" rounded color='#677d35'  @click='check' dark) 
            v-btn(v-else rounded color='#677d35'  @click='e6 = 3' href='#' dark) 
          v-stepper-step(:complete='e6 > 3' step='3' color="#677d35") 
          v-stepper-content(step='3')
            v-card.step3.mb-12
              validation-observer( ref="observer" v-slot="{ invalid }")
                v-card-text
                  validation-provider(v-slot="{ errors }" rules="required")
                    v-text-field(
                        color="#000000"
                        v-model="project"
                        filled
                        rounded
                        readonly
                        prefix=' ')
                  validation-provider(v-slot="{ errors }" rules="required")
                    v-text-field(
                      color="#000000"
                      v-model="date"
                      filled
                      rounded
                      readonly
                      prefix=' ')
                  validation-provider(v-slot="{ errors }" rules="required")
                    v-text-field(
                      color="#000000"
                      v-model="photographer"
                      filled
                      rounded
                      readonly
                      prefix=' ')

                  v-btn(rounded color='#677d35' @click='e6 = 2' href='#' dark) 
                  v-btn(:disabled="invalid" rounded color='#677d35' type="submit" dark) 
</template>

<script>
import { required } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'

setInteractionMode('eager')

extend('required', {
  ...required,
  message: '{_field_} can not be empty'
})

export default {
  data () {
    return {
      e6: 1,
      modal: false,
      date: null,
      agreeStatement: false,
      project: '',
      photographer: null
    }
  },
  components: {
    ValidationProvider,
    ValidationObserver
  },
  methods: {
    check () {
      if (this.$data.agreeStatement === false) {
        this.$swal({
          title: ''
        })
      } else if (this.$data.photographer === null) {
        this.$swal({
          title: ''
        })
      } else if (this.$data.date === null) {
        this.$swal({
          title: ''
        })
      }
    },
    onSubmit () {
      this.$refs.observer.validate()
      this.axios.post(process.env.VUE_APP_API + '/users/order/' + this.$store.state.user.id, this.$data)
        .then(res => {
          if (res.data.success) {
            this.$swal({
              icon: 'success',
              title: '',
              showConfirmButton: false,
              timer: 1000
            })
            res.data.result.src = process.env.VUE_APP_API + '/albums/file/' + res.data.result.file
            setTimeout(() => {
              this.$router.push({ path: '/user/order', name: 'OrderList' })
            }, 1000)
          } else {
            this.$swal({
              icon: 'error',
              title: '',
              text: res.data.message
            })
          }
        })
        .catch(err => {
          this.$swal({
            icon: 'error',
            title: '',
            text: err.response.data.message
          })
        })
    }
  }
}
</script>
